body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  padding: 30px;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
}

#page-content {
  width: 100%;
  max-width: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  color: #333;
  margin-top: 50px;
}

h2 {
  color: #333;
}

button {
  background-color: #007BFF;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
  border-radius: 20px;
  box-shadow: 0px 8px 15px rgba(255, 255, 255, 0.1);
}

button:hover {
  background-color: #008CBA;
  color: white;
  box-shadow: 0px 15px 20px rgba(255, 255, 255, 0.3);
  transform: translateY(-3px);
}

.cards-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%;
}
div.card {
  background-color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: 200px;
  height: 300px;
  margin-bottom: 15px;
  border-radius: 5px; 
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: auto;
  margin: 10px;
}

div.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

.container {
  padding: 10px 16px;
}

.arrows {
  display: flex;
  justify-content: space-between;
  margin: 0 -10px; /* Adjust as needed to extend arrows to the edge of the card */
}

.arrow {
  cursor: pointer;
  user-select: none;
  font-size: 18px;
  padding: 10px; /* Add some padding around arrows for larger click area */
  transition: all 0.3s ease-in-out;
}

.arrow.left {
  color: #333;
}

.arrow.right {
  color: #333;
}

.arrow:hover {
  color: #555;
  transform: scale(1.1);
}

/* Update dark mode styles for arrows */
body.dark-mode .arrow {
  color: #fff;
}

body.dark-mode .arrow:hover {
  color: #aaa;
}

body.dark-mode {
  background-color: #333;
  color: #fff;
}

body.dark-mode h1 {
  color: #fff;
}

body.dark-mode h2 {
  color: #fff;
}

body.dark-mode button {
  background-color: #204383;
  color: #fff;
}

body.dark-mode button:hover {
  background-color: #295dbe;
}

body.dark-mode div.card {
  background-color: #444;
}


div.card > div:last-child {
  display: flex;
  justify-content: center;
}

.settings-menu {
  display: flex;
  flex-direction: column;
  position: fixed; /* Change from absolute to fixed */
  top: 10px;
  right: 10px;
  z-index: 999; /* Ensures the button stays on top */
}


.settings-menu summary {
  list-style: none; 
}

.settings-menu summary::-webkit-details-marker { 
  display: none;
}

.settings-menu i {
  font-size: 24px;
}

.settings-menu details {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}


button.donate-button {
  background-color: yellow;
  color: blue;
  margin-top: 10px; /* Optional: add some space between the buttons */
}

button.donate-button:hover {
  background-color: #ffdd00;  /* darken the yellow a bit when hovered */
  color: navy;  /* darken the blue a bit when hovered */
}

/* Dark mode styles */
body.dark-mode button.donate-button {
  background-color: #838110;
  color: #fff;
}

body.dark-mode button.donate-button:hover {
  background-color: #3d3507;
}

button.discard-button {
  background-color: red;
  color: white;
}

button.discard-button:hover {
  background-color: darkred;
  color: white;
}

/* Dark mode styles */
body.dark-mode button.discard-button {
  background-color: #600;
}

body.dark-mode button.discard-button:hover {
  background-color: #900;
}

.card {
  width: 200px;
  border: 1px solid black;
  margin: 10px;
  padding: 10px;
}

.card.total-domination {
  width: 300px;
}

.switch-mode {
  position: fixed; /* Change from absolute to fixed */
  top: 10px;
  left: 10px;
  z-index: 999; /* Ensures the button stays on top */
}

@media (max-width: 600px) {
  #page-content {
    max-width: 100%;
    padding: 10px;
  }

  h1 {
    font-size: 24px;
    margin-top: 20px;
  }

  h2 {
    font-size: 18px;
  }

  .cards-container {
    flex-direction: column;
    align-items: center;
  }

  div.card {
    width: 90%;
    margin-bottom: 20px;
  }
}